import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Events } from '@ionic/angular';
import { TranslateService } from '@ngx-translate/core';

import { FormBuilder, FormGroup } from '@angular/forms';

// @ts-ignore => yes its there otherwise it wouldn't compile
import { UserSandbox } from '@sandboxes/user.sandbox';
// import { RegisterPage } from '../register/register';

@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {

  subscription;
  credentialsForm: FormGroup;

  constructor(private router: Router,
              private formBuilder: FormBuilder,
              private userSandbox: UserSandbox,
              private events: Events,
              public translate: TranslateService
  ) {
      this.credentialsForm = this.formBuilder.group({
        email: [''],
        password: ['']
      });
  }

  ngOnInit() {
    this.subscription = this.userSandbox.userError$.subscribe((failed: boolean) => {
      // TODO: show error
    });
    this.events.subscribe('login:success', () => {
      this.router.navigateByUrl('/app');
    });
  }

  onSignIn() {
    this.userSandbox.login(this.credentialsForm.value['email'], this.credentialsForm.value['password']);
  }

  onRegister() {
    this.router.navigate(['register']);

  }

}
